<template>
  <div>
    <Headers @getmyInfo="userandid" ref="headcom"></Headers>
    <!-- <Chat ref="chatchild" :turenameres="turenameres" :turenamereq="turenamereq"></Chat> -->
    <div class="Tpersons">
      <div class="container"><br>
      <div class="row Tsearch_box">
        <div class="col-md-12 row">
          <el-input type="text"  v-model="searchuser" class="col-md-2" placeholder=""></el-input>
          <el-input type="text"  v-model="searchclass" class="col-md-2" placeholder=""></el-input>
          <el-input type="text"  v-model="searchsnum" class="col-md-2" placeholder=""></el-input>
          <!-- <el-input type="text"  v-model="searchbuild" class="col-md-2" placeholder=""> -->
          <el-select v-model="searchbuild" class="col-md-2" clearable>
            <el-option value="1" label=""></el-option>
            <el-option value="2" label=""></el-option>
            <el-option value="3" label=""></el-option>
            <el-option value="4" label=""></el-option>
            <el-option value="5" label=""></el-option>
            <el-option value="6" label=""></el-option>
          </el-select>
          <el-input type="text"  v-model="searchhome" class="col-md-2" placeholder=""></el-input>
          <el-button type="primary" @click="search()"></el-button>
          <el-button type="success" @click="addStudent()"></el-button> 
        </div>
    <div class="table-responsive"><br>
        <h6></h6>     
      <table class="table table-bordered">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>            
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>            
          </tr>
        </thead>
          <tbody>
    <tr v-for="tableData in tableDatas">
      <td>{{tableData.userName}}</td>
      <td>{{tableData.number}}</td>
      <td>{{tableData.sex | sexFilter}}</td> 
      <td>{{tableData.old}}</td>
      <td>{{tableData.phone}}</td>
      <td>{{tableData.classes}}</td>
      <td>{{tableData.buildNum | buildNumFilter}}</td>
      <td>{{tableData.homeNum}}</td>
      <td :class="{colorRed:tableData.isstate}">{{tableData.isstate==false ? "" : ""}}</td>
      <td>
        <!-- {{tableData.tokenId}} -->
        <template>
          <!-- <button class="btn btn-success btn-sm"  @click="chatShow(tableData)" data-toggle="modal" data-target="#charModal"></button>                     -->
          <button class="btn btn-primary btn-sm"  @click="editStudent(tableData)"></button>
          <button class="btn btn-success btn-sm"  @click="editRow(tableData)"></button>
          <button class="btn btn-danger btn-sm"  @click="delRow(tableData)"></button>
        </template>  
      </td>
    </tr>
  </tbody>
      </table>
    </div>
    <div class="table_bottom">
            <!-- <div> {{totles}} </div> -->
			<div class="pages">
				<el-pagination @current-change="pageStarts" :current-page.sync="currentPage1" :page-size="6" layout="total, prev, pager, next" :total="totles">
				</el-pagination>
			</div>
    </div>
  </div>
  </div>

    </div>
    <div class="add_box">
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="60%">
          <el-form :model="form" label-width="80px" size="mini" :rules="rules" ref="ruleForm">
            <el-form-item label="" prop="name">
              <el-input v-model="form.name" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="number">
              <el-input v-model="form.number" ></el-input>
            </el-form-item>
            <el-form-item label= prop="sex">
              <el-radio-group v-model="form.sex">
                <el-radio label="1"></el-radio>
                <el-radio label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="old">
              <el-input v-model="form.old" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="form.phone" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="buildNum">
              <el-select v-model="form.buildNum">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="5"></el-option>
                <el-option label="" value="6"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="classes">
              <el-input v-model="form.classes" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="homeNum">
              <el-input v-model="form.homeNum" type="number"></el-input>
            </el-form-item>
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="sumbitForm()"></el-button>
        </span>
      </el-dialog>

    </div>
  </div>
</template>

<script>
import Headers from "./headers";
// import Chat from "./chat"
import Axios from "axios";
import { store } from "../vuex/store.js";
import Element from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import Vue from "vue";
Vue.use(Element);
export default {
  store,
  name: "news",
  data() {
    return {
      searchuser: "",
      searchclass: "",
      searchsnum: "",
      searchbuild:"",
      searchhome:"",
      pageStart: 1,
      totles: 1,
      tableDatas: [],
      currentPage1: 1,
      _idreq:'',
      _idres:'',
      turenamereq:'',
      turenameres:'',
      dialogVisible:false,
      form:{
        
      },
      rules:{
        // name:[
        //   { required: true, message: '', trigger: 'blur' },
        // ]
      }
    };
  },
  filters:{
    sexFilter(val){
      if(val=='0'){
        return ""
      }else{
        return ""
      }
    },
    buildNumFilter(val){
      if(val=='1'){
        return ''
      }else if(val=='2'){
        return ''        
      }else if(val=='3'){
        return ''        
      }else if(val=='4'){
        return ''        
      }else if(val=='5'){
        return ''        
      }else if(val=='6'){
        return ''        
      }
    }
  },
  methods: {
    getStudentList() {
      var _this = this;
      Axios({
        url: "/VueHandler/AdminHandler?action=usershow",
        method: "post",
        data: {
          pageStart: _this.pageStart,
          username: _this.searchuser,
          classes: _this.searchclass,
          number: _this.searchsnum,
          buildNum: _this.searchbuild,
          homeNum: _this.searchhome,
        }
      }).then(function(res) {
        if (res.data.success) {
          _this.tableDatas = res.data.data.list;
          _this.totles = res.data.data.count;
          // console.log(_this.tableDatas)
        } else {
          _this.$message("");
        }
      });
    },
    //
    pageStarts(val) {
      //console.log(`: ${val}`);
      this.pageStart = val;
      this.getStudentList(this.pageStart);
    },
    search() {
      this.getStudentList();
    },
    addStudent() {
      this.form={
        sex:'1',
        buildNum:'1'
      }
      this.dialogVisible = true
    },
    editStudent(row) {
        var _this=this
    //   console.log(row);
        Axios({
          url: "/VueHandler/AdminHandler?action=lockuser",
          method: "get",
          params:{
              tokenId:row.tokenId
          }
        }).then(function(res){
            // console.log(res.data)
            this.getStudentList()
        }.bind(this))
    },
    userandid(user){
      this.turenamereq=user[0],
      this._idreq=user[1]
      // console.log(this._idreq,this.turenamereq)
    },
    sumbitForm(){
      let _this = this
      let datas = {
            username:this.form.name,
            sex:this.form.sex,
            old:this.form.old,       
            phone:this.form.phone,       
            buildNum:this.form.buildNum,       
            classes:this.form.classes,       
            homeNum:this.form.homeNum,       
            number:this.form.number,       
          }
      if(this.form.tokenId){
        datas.tokenId=this.form.tokenId
        Axios({
          url:'/VueHandler/AdminHandler?action=edituser',
          method:'post',
          data:datas
        }).then(function(res){
          if(res.data.success){
            _this.$message("");
            _this.getStudentList()                    
            _this.dialogVisible = false
          }else{
            _this.$message("");          
            return
          }
        })
      }else{
        Axios({
          url:'/VueHandler/AdminHandler?action=adduser',
          method:'post',
          data:datas
        }).then(function(res){
          if(res.data.success){
            _this.$message("");
            _this.getStudentList()                    
            _this.dialogVisible = false
          }else{
            _this.$message("");          
            return
          }
        })
      }
    },
    editRow(row){
      console.log(row);
      this.dialogVisible = true
      this.form = Object.assign({},this.form,{
        tokenId : row.tokenId,
        name : row.userName,
        sex : row.sex,
        old : row.old,
        phone : row.phone,
        buildNum : row.buildNum,
        classes : row.classes,
        homeNum : row.homeNum,
        number : row.number,
      })
    },
    delRow(row){
      console.log(row.tokenId);
      let _this = this
      this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          Axios({
            url:'/VueHandler/AdminHandler?action=delStudent',
            method:'get',
            params:{
              tokenId:row.tokenId
            }
          }).then(function(res){
            console.log(res.data)
            if(res.data.success){
              _this.getStudentList()
              _this.$message("")
            }
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });      
    }
  },
  components: { Headers },
  computed: {
    studentList() {
      return this.$store.state.studentList;
    }
  },
  mounted() {
    this.getStudentList();
  }
};
</script>

<style scoped>
.form-control{
  margin: 0 0px;
}
</style>



